body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.gallery-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.gallery {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-gap: 1rem;
  margin-top: 3rem;
  width: 70%;
  justify-items: center;
  background: #85817f96;
  padding: 1rem;
}
.gallery__img {
  object-fit: cover;
}
.gallery__img:hover {
  transition: all 0.2s ease-in-out;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}
.input-form-wrapper {
  font-size: 1.2rem;
}
.search-input--large {
  width: 25rem;
  height: 100%;
  font-size: inherit;
  padding-left: 0.5rem;
}

.search-btn--large {
  width: 10rem;
  height: 2.5rem;
  font-size: inherit;
  background-color: #2736e8;
  border: none;
  color: #fff;
  margin-left: 1rem;
  border: 1px solid #eee;
  cursor: pointer;
}

.search-btn--large:hover {
  background-color: #3e518e;
}

.book-details--container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  text-align: center;
  padding: 1rem;
  height: calc(100% - 2rem);
}

.book-details--container__item1 {
  padding: 1rem;
  background: #e8e5e4;
}
.item1 {
  margin: 1rem;
}
.book-details--container__item2 {
  padding: 1rem;
  background: #e8e5e4;
  color: #000;
  text-align: left;
}
.rating-wrapper {
  padding: 1rem;
  display: inline-block;
}
.p1 {
  padding: 1rem;
}

html * {
  font-size: 16px;
  font-family: sans-serif;
}

.App {
  text-align: center;
  position: relative;
}

#root {
  height: calc(100vh);
  background-image: url(/react-hooks-search-goodreads/static/media/bg.8066800e.jpeg);
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  background: #fff;
}

fieldset {
  border: none;
  height: 2rem;
}
.f2 {
  font-size: 2rem;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

